@import url(https://fonts.googleapis.com/css2?family=Rampart+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{position:relative;min-height:100vh;width:100%;background:#082032;font-family:"Lato", sans-serif}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background:rgba(155,155,155,0.5);border-radius:20px;border:transparent}.App{width:100%;min-height:100vh;position:reltive}.App .app-title{height:30vh;display:flex;justify-content:center;align-items:center;font-size:10vw;font-family:"Rampart One", cursive;color:#fff}.input-container{position:relative;width:100%;height:10vh;display:flex;justify-content:center;align-items:center;grid-gap:1rem;gap:1rem}input[type="text"]{-webkit-appearance:none;background:#2c394b;border:none;width:50%;height:3rem;padding:0.5rem;font-weight:bolder;font-size:1.5rem;color:#fff}input[type="text"]:focus{outline:none}.input-button{width:3rem;height:3rem;-webkit-appearance:none;background:#fff;color:#082032;border:none;font-weight:bolder;font-size:1.5rem;transition:all 0.5s ease}.input-button:hover{color:#fff;background:#2c394b}@media screen and (max-width: 768px){input[type="text"]{width:60%;padding:1rem;border-radius:1.5rem}.input-button{border-radius:50%}}.list-container{width:100%;min-height:50vh;display:flex;flex-direction:column;justify-content:center;align-items:center;padding:5vh 1vw;grid-gap:1rem;gap:1rem}@media screen and (max-width: 768px){.list-container{justify-content:flex-start}}.item-container{position:relative;width:75%;height:4rem;display:flex;justify-content:space-between;align-items:center;grid-gap:1rem;gap:1rem}.item-container .todo{position:relative;width:100%;height:100%;display:flex;justify-content:space-between;align-items:center;grid-gap:1rem;gap:1rem;background:#334756}.item-container .todo .todo-content{position:relative;height:100%;width:100%;padding:0.5rem;color:#fff;display:flex;justify-content:flex-start;align-items:center;font-size:1.5rem;transition:all 0.5s ease}.item-container .todo .date-time{height:100%;width:6.5rem;color:#6b7aa1;display:flex;justify-content:flex-end;align-items:center}.item-container .todo-btn{height:100%;width:9rem;display:flex;justify-content:space-between;align-items:center;grid-gap:1rem;gap:1rem}.item-container .todo-btn button{-webkit-appearance:none;border:none;width:4rem;height:100%;color:#fff;font-size:1.5rem;transition:all 0.5s ease}.item-container .todo-btn button:first-child{background:#c2f784}.item-container .todo-btn button:first-child:hover{color:#c2f784;background:#fff}.item-container .todo-btn button:last-child{background:#ff4848}.item-container .todo-btn button:last-child:hover{color:#ff4848;background:#fff}.completed .todo{background:#415a6d}.completed .todo .todo-content{text-decoration:line-through;color:#a3a3a3}.completed .todo .date-time{color:#92ffa1}.completed .todo-btn button:first-child{background:#c95c13}@media screen and (max-width: 768px){.item-container{width:90%;height:9rem;flex-direction:column}.item-container .todo{border-radius:2rem;padding:0 1rem}.item-container .todo .date-time{padding-right:0.5rem}.item-container .todo-btn button{border-radius:50%}}@media screen and (max-width: 768px){.App .app-title{font-size:10vh}}

